@import "../../main.scss";

.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: $white;
    user-select: none;
    border-radius: 20px;
    padding: 20px;
    min-width: 220px;
    color: $graphite;
}

.top {
    position: relative;
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}

.status {
    color: $dominant;
    margin-left: 16px;
    display: flex;
    align-items: center;

    &::before {
        content: '';
        position: absolute;
        left: 0;
        height: 10px;
        width: 10px;
        background-color: $dominant;
        border-radius: 5px;
        margin-bottom: 2px;
    }
}

.favorites {
    cursor: pointer;
}

.description {
    width: 100%;
}

.name {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 6px;
}

.rating {
    display: flex;
    margin-bottom: 6px;
}

.reviewers {
    margin-left: 5px;
}

.price {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 20px;
}

.order {
    display: flex;
    justify-content: space-between;
}

.count {
    display: flex;
    height: 30px;
    align-items: center;
    background-color: $grey;
    border-radius: 15px;
    min-width: 80px;
    justify-content: space-between;
    font-weight: 700;
}

.go {
    padding: 0 10px;
    font-size: 20px;
}

.add {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    height: 30px;
    width: 80px;
    background-color: $dominant;
    font-weight: 700;
    color: $white;
    cursor: pointer;
    &:hover {
        box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    }
}