@import "../../main.scss";


.wrapper {
    position: relative;
    width: 100%;
}

.section {
    position: relative;
    height: 100vh;
    overflow: hidden;
}

.treeBackground {
    position: absolute;
    top: -30vh;
    left: 50vw;
    z-index: -10;
}

.container {
    padding-top: $headerHeight;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}

.description {
    margin-top: 15vh;
    color: $graphite;
    margin-left: $desctop1Indentation;
    margin-right: $desctop1Indentation*2;
    
    @include desktop1440 {
        margin-top: 10vh;
        margin-left: $desctop2Indentation;
        margin-right: $desctop2Indentation*2;
    }
}

.title {
    font-size: 48px;
    margin-bottom: 30px;
}

.descriptionText {
    max-width: 60vw;
    font-size: 20px;
    font-weight: 400;
}
